___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

input,
button,
textarea,
select {
  margin: 0;
  font: inherit;
}

.app.dark {
  --bg-color: #090949;
  --inverted-bg-color: #e8e8ea;
  --primary-color: #04ff04;
  --secondary-color: #049604;
  --inverted-primary-color: #0232c2;
  --inverted-secondary-color: #0452ff;
}

:root {
  --bg-color: #e8e8ea;
  --inverted-bg-color: #090949;
  --primary-color: #0232c2;
  --secondary-color: #0449e0;
  --inverted-primary-color: #04ff04;
  --inverted-secondary-color: #049604;
}

:root {
  --font-family-main: "Consolas", "Times New Roman", "serif";
  --font-size-m: 16px;
  --font-line-m: 24px;
  --font-m: var (\`\${--font-size-m}\`) / var (\`\${--font-line-m}) var
    (\`\${--font-family-main});
  --font-size-l: 24px;
  --font-line-l: 32px;
  --font-1: var (\`\${--font-size-l}\`) / var (\`\${--font-line-l}) var
    (\`\${--font-family-main});
  --navbar-height: 50px;
  --sidebar-width: 300px;
  --sidebar-width-collapsed: 80px;
  --modal-z-index: 10;
  --overlay-color: rgba(0 0 0 / 60%);
  --red-light: #ff00;
  --red-dark: #ce0505;
}

body {
  font: var(--font-m);
  color: var(--primary-color);
}

.app {
  background: var(--bg-color);
  min-height: 100vh;
}

.content-page {
  display: flex;
}

.page-wrapper {
  flex-grow: 1;
  padding: 20px;
